


























import Vue from 'vue';
import liff from '@line/liff';
import CheckIcon from '@/assets/check-icon.svg';

export default Vue.extend({
  data() {
    return {
      CheckIcon,
    };
  },
  computed: {
    memberGroupCode(): null | string {
      return this.$store.state.memberGroupCode;
    },
    isLineClient(): boolean {
      if (liff.isInClient()) {
        return true;
      }
      return false;
    },
  },
  methods: {
    closePage() {
      liff.closeWindow();
    },
  },
});
